export const routes = [
    {
        path: '/main',
        name: 'Main',
        component: r => require(['../views/Main.vue'], r),
        children: [
          {
            path: 'home',
            name: 'Home',
            component: r => require(['../views/main/Home.vue'], r)
          },
          {
            path: 'classification',
            name: 'Classification',
            component: r => require(['../views/main/Classification.vue'], r)
          },
          {
            path: 'shopcart',
            name: 'Shopcart',
            component: r => require(['../views/main/Shopcart.vue'], r)
          },
          {
            path: 'my',
            name: 'My',
            component: r => require(['../views/main/My.vue'], r)
          }
        ]
    },
    {
      path: '/search/:cat_name?:cid?',
      name: 'Search',
      component: r => require(['../views/Search.vue'], r)
    },
    {
      path: '/detail/:goods_id?',
      name: 'Detail',
      component: r => require(['../views/Detail.vue'], r)
    },
    {
      path: '/goSearch',
      name: 'GoSearch',
      component: r => require(['../views/GoSearch.vue'], r)
    },
    {
      path: '/goList',
      name: 'GoList',
      component: r => require(['../views/GoList.vue'], r)
    },
    {
      path: '/order',
      name: 'Order',
      component: r => require(['../views/Order.vue'], r)
    },
    {
      path: '/address',
      name: 'Address',
      component: r => require(['../views/address/Address.vue'], r)
    },
    {
      path: '/addressAdd',
      name: 'AddressAdd',
      component: r => require(['../views/address/AddressAdd.vue'], r)
    },
    {
      path: '/addressEdit',
      name: 'AddressEdit',
      component: r => require(['../views/address/AddressEdit.vue'], r)
    },
    {
      path: '/orderList',
      name: 'OrderList',
      component: r => require(['../views/OrderList.vue'], r),
      children: [
        {
          path: 'orderPay',
          name: 'OrderPay',
          component: r => require(['../views/order/OrderPay.vue'], r)
        },
        {
          path: 'orderGoods',
          name: 'OrderGoods',
          component: r => require(['../views/order/OrderGoods.vue'], r)
        },
        {
          path: 'orderEnd',
          name: 'OrderEnd',
          component: r => require(['../views/order/OrderEnd.vue'], r)
        },
        {
          path: 'orderAll',
          name: 'OrderAll',
          component: r => require(['../views/order/OrderAll.vue'], r)
        }
      ]
  },
    {
        path: '*',
        redirect: {
            name: 'Home'
        }
    }
]