export default {
    namespaced:true,
    state:{
        lists:[
            {
                id: '1',
                name: '张三',
                tel: '10000000000',
                address: '广东省广州市天河区xxx',
                isDefault: true,
                province:"浙江省",
                city:"杭州市",
                county:"西湖区",
                areaCode:"440106",
                addressDetail:"xxx"
            }
        ],
    },
    mutations:{
        add(state,obj){
            state.lists.forEach((item)=>{
                item.isDefault = false;
            })
            obj.isDefault = true;
            obj.id = state.lists.length + 1;
            state.lists.push(obj); 
        },
        edit(state,obj){
            let index = state.lists.findIndex(item=>item.id == obj.id);
            Object.assign(state.lists[index],obj);
        },
        editId(state,id){
            state.lists.forEach((item)=>{
                item.isDefault = item.id == id?true:false;
            })

        }
    },
    actions:{}
}